import { lazy, Suspense } from 'react';
import {HashRouter, Route, Routes} from 'react-router-dom';

const Home = lazy(()=>import('./profile_pages/Home'));
const ProfileLayout = lazy(()=>import('./ProfileLayout'));
const Intro = lazy(()=>import('./profile_pages/Intro'));
const AboutMe = lazy(()=>import('./profile_pages/AboutMe'));
const Portfolio = lazy(()=>import('./profile_pages/Portfolio'));
const Contact = lazy(()=>import('./profile_pages/Contact'));
const Outro = lazy(()=>import('./profile_pages/Outro'));

// const { PUBLIC_URL } = process.env;

const Profile = ()=>{
    return (
        <HashRouter basename={process.env.PUBLIC_URL}>
            <Suspense fallback={<ProfileLayout />}>
                <Routes>

                    {/* 공통레이아웃 설정 */}
                    <Route element={<ProfileLayout />}>

                        {/* 각 페이지에 대한 라우팅 설정 */}
                        <Route index element={<Home />} />
                        <Route path="/aboutme" element={<AboutMe />} />
                        <Route path="/Intro" element={<Intro />} />
                        <Route path="/Portfolio" element={<Portfolio />} />
                        <Route path="/contact" element={<Contact />} />
                        <Route path="/outro" element={<Outro />} />
                    </Route>
                </Routes>
            </Suspense>
         </HashRouter>
    );
}

export default Profile;